import React, { useEffect, useState } from "react";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import { useSelector, useDispatch } from "react-redux";
import {
  selectMoviesList,
  updateBinge,
  selectLoginCredential,
  selectLanguageObj,
  getAllMovies,
  addToWatch,
} from "../../redux/movies";
import i18n from "i18next";
import { useTranslation, initReactI18next } from "react-i18next";
import { enObj, frObj } from "../../Assets/translation";
import axios from "axios";

const translationsEn = enObj;

const translationsFr = frObj;

i18n
  .use(initReactI18next) // passes i18n down to react-i18next
  .init({
    resources: {
      en: { translation: translationsEn },
      fr: { translation: translationsFr },
    },
    lng: "en",
    fallbackLng: "en",
    interpolation: { escapeValue: false },
  });

const Home = () => {
  const dispatch = useDispatch();
  let AllMovie = useSelector(selectMoviesList);
  useEffect(() => {
    dispatch(getAllMovies());
  }, []);
   
  const { t } = useTranslation();

  let loginObj = useSelector(selectLoginCredential);
  const language = useSelector(selectLanguageObj);
  const [id,setId] = useState("");
  const [Title, setTitle] = useState("");
  const [Year, setYear] = useState("");
  const [Runtime, setRuntime] = useState("");
  const [Genre, setGenre] = useState("");
  const [Country, setCountry] = useState("");
  const [pic, setpic] = useState("");

  const onTitleChng = (e) => {
    setTitle(e.target.value);
  };
  const onYearChng = (e) => {
    setYear(e.target.value);
  };
  const onRuntimeChng = (e) => {
    setRuntime(e.target.value);
  };
  const onGenreChng = (e) => {
    setGenre(e.target.value);
  };
  const onCountryChng = (e) => {
    setCountry(e.target.value);
  };
  const onPicChng = (e) => {
    setpic(e.target.value);
  };

  const onSubmit = () => {
    return null;
  };

  useEffect(() => {
    if (language.translate) {
      i18n.changeLanguage("fr");
    }
  }, [language.translate]);

  

  let Movie = AllMovie;

  const onWatchNow = (movie_id) => {
    dispatch(addToWatch(loginObj.id, movie_id));
  };

  const addToBinge = (movie_id) => {
    dispatch(updateBinge(movie_id, loginObj.id));
  };

  const onDelete = async (movie_id) => {
    axios
      .post("http://3.109.143.147:4000//api/v1/delete", {
        id: movie_id,
      })
      .then(() => {
        window.alert("Movie deleted sucessfully");
      });
    dispatch(getAllMovies());
  };

  // modal
  const [show, setShow] = useState(false);
  const handleClose = async () => {
    setShow(false);
    await fetch("http://3.109.143.147:4000//api/v1/update", {
      method: "PUT",
      body: JSON.stringify({
        _id: id,
        Title: Title,
        Year: Year,
        Runtime: Runtime,
        Country: Country,
        Genre: Genre,
        pics: pic,
      }),
      headers: {
        "Content-type": "application/json; charset=UTF-8",
      },
    })
  }

  const handleShow = (movie_id) => {
    AllMovie.filter((ele) => {
      if( ele._id === movie_id){
        setTitle(ele.Title);
        setYear(ele.Year);
        setRuntime(ele.Runtime);
        setGenre(ele.Genre);
        setCountry(ele.Country);
        setpic(ele.pics);
        setId(ele._id);
      }
    });
    setShow(true);
  }

  return (
    <div>
      {/* modal jsx */}
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Modal heading</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div style={{ textAlign: "center" }}>
          <h2 className={"fw-bold mb-2 text-uppercase"}>Edit Movie</h2>
            <form>
             
              <label className={"form-label"}>Title</label>
              <input
                onChange={onTitleChng}
                className={"form-control form-control-lg"}
                type="email"
                id="typeEmailX"
                value={Title}
              />

              <label className={"form-label"}>Year</label>
              <input
                className={"form-control form-control-lg"}
                id="typeEmailX"
                onChange={onYearChng}
                value={Year}
              />

              <label className={"form-label"}>Runtime</label>
              <input
                className={"form-control form-control-lg"}
                id="typeEmailX"
                onChange={onRuntimeChng}
                value={Runtime}
              />

              <label className={"form-label"}>Genre</label>
              <input
                className={"form-control form-control-lg"}
                id="typeEmailX"
                onChange={onGenreChng}
                value={Genre}
              />

              <label className={"form-label"}>Country</label>
              <input
                className={"form-control form-control-lg"}
                id="typeEmailX"
                onChange={onCountryChng}
                value={Country}
              />

              <label className={"form-label"}>Image Url</label>
              <input
                className={"form-control form-control-lg"}
                id="typeEmailX"
                onChange={onPicChng}
                value={pic}
              />

              <button
                onClick={onSubmit}
                className={"btn btn-outline-light btn-lg px-5"}
                type="submit"
              >
                Submit
              </button>
            </form>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={handleClose}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>

      {Movie.map((ele) => {
        return (
          <div key={ele._id} style={{ margin: "10px 10px 10px 350px" }}>
            <div className={"row row-cols-2 g-2 m-2  "}>
              <div className={"col"}>
                <div className={"card"}>
                  <img
                    src={ele.pics}
                    className={"card-img-top"}
                    alt="Palm Springs Road"
                  />
                  <div className={"card-body"}>
                    <h5 className={"card-title"}>{ele.Title}</h5>
                    <p className={"card-text"}>Genre:{ele.Genre}</p>
                    <p className={"card-text"}>
                      <span>Year:</span> {ele.Year}
                    </p>

                    <p className={"card-text"}>Country:{ele.Country}</p>

                    <p className={"card-text"}>Duration:{ele.Runtime}</p>
                  </div>

                  {loginObj.id !== "admin" ? (
                    <div style={{ textAlign: "center" }}>
                      {" "}
                      <button
                        className={"btn bg-dark btn-outline-success"}
                        type="submit"
                        onClick={() => onWatchNow(ele._id)}
                      >
                        {t("watching_now")}
                      </button>
                      <button
                        className={"btn bg-dark btn-outline-success"}
                        type="submit"
                        onClick={() => {
                          addToBinge(ele._id);
                        }}
                      >
                        {t("binge_bucket")}
                      </button>
                    </div>
                  ) : (
                    <div style={{ textAlign: "center" }}>
                      <button
                        className={"btn bg-dark btn-outline-success"}
                        type="submit"
                        onClick={() => {
                          onDelete(ele._id);
                        }}
                      >
                        Delete Movie
                      </button>

                      <button
                        className={"btn bg-dark btn-outline-success"}
                        type="submit"
                        onClick={() => {
                          handleShow(ele._id);
                        }}
                      >
                        Edit Movie
                      </button>
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        );
      })}
    </div>
  );
};

export default Home;
