import React from "react";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { handleLoginLogout } from "../../redux/movies";
import { setLoginObj } from "../../redux/movies";
import axios from "axios";
import { Link } from "react-router-dom";

const Login = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [email, setEmail] = useState("");
  const [pass, setPass] = useState("");

  const onEmailChange = (e) => {
    setEmail(e.target.value);
  };

  const onPasswordChange = (event) => {
    setPass(event.target.value);
  };

  const validateUser = async()=>{
   const response =  await axios.post('http:3.109.143.147:4000/api/v1/login',{
      username:email,
      password:pass,
    });
    
    return response.data.valid
  }



  const onlogin = async () => {
    let authres =  await validateUser();
    if(authres){
      dispatch(setLoginObj(email, pass));
      dispatch(handleLoginLogout(authres));
      navigate("/home");
    }
    else window.alert("Invalid User");
  }

  return (
    <div>
      <section className={"vh-10 gradient-custom"}>
        <div className={"container py-1 h-10"}>
          <div
            className={
              "row d-flex justify-content-center align-items-center h-100"
            }
          >
            <div className={"col-12 col-md-8 col-lg-6 col-xl-5"}>
              <div
                className={"card bg-primary text-white"}
                style={{ border_radius: "1rem" }}
              >
                <div className={"card-body p-5 text-center"}>
                  <div className={"mb-md-5 mt-md-4 pb-5"}>
                    <h2 className={"fw-bold mb-2 text-uppercase"}>Welcome</h2>

                    <div className={"form-outline form-white mb-2"}>
                      <input
                        onChange={onEmailChange}
                        className={"form-control form-control-lg"}
                        type="email"
                        id="typeEmailX"
                      />
                      <label value={email} className={"form-label"}>Email</label>
                    </div>

                    <div className={"form-outline form-white mb-2"}>
                      <input
                        onChange={onPasswordChange}
                        className={"form-control form-control-lg"}
                        type="password"
                        id="typeEmailX"
                        value = {pass}
                      />
                      <label className={"form-label"}>Password</label>
                    </div>

                    <p className={"small mb-5 pb-lg-2"}>
                      <a className={"text-white-50"} href="#!">
                        Forgot password?
                      </a>
                    </p>

                    <button
                      onClick={onlogin}
                      className={"btn btn-outline-light btn-lg px-5"}
                      type="submit"
                    >
                      Login
                    </button>

                    <div
                      className={
                        "d-flex justify-content-center text-center mt-2 pt-1"
                      }
                    >
                      <a href="#!" className={"text-white"}>
                        <i className={"fab fa-facebook-f fa-lg"}></i>
                      </a>
                      <a href="#!" className={"text-white"}>
                        <i className={"fab fa-twitter fa-lg mx-4 px-2"}></i>
                      </a>
                      <a href="#!" className={"text-white"}>
                        <i className={"fab fa-google fa-lg"}></i>
                      </a>
                    </div>
                  </div>

                  <div>
                    <p className={"mb-0"}>
                      Don't have an account?{" "}
                      <Link className={"text-white-50 fw-bold"} to={"signup"}>Signup</Link>
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Login;
