import { BrowserRouter, Routes, Route } from "react-router-dom";
import Login from "./components/Login/login";
import Navbar from "./components/Navbar/navbar";
import Home from "./components/Home/home";
import Watching_now from "./components/Watching_now/watching_now";
import Binge_bucket from "./components/Binge_bucket/binge_bucket";
import Recommended from "./components/Recomended/recommended";
import { useSelector } from "react-redux";
import { loginStatus} from "./redux/movies";
import Protected from "./components/protected/protected";
import Signup from "./components/Signup/signup";
import Addmovies from "./components/AdminDashBoard/addmovies";


function App() {
  const userLoggedIn = useSelector(loginStatus);
  
  return (
    <div>
      <BrowserRouter>
      {userLoggedIn.isLoggedIn && <Navbar/>}
        <Routes>
          <Route path="/" element={<Login />} />
          <Route path={'home'} element={<Protected component = {<Home/>}/>}/>
          <Route path={'Watching_Now'} element={<Protected component = {<Watching_now/>}/>}/>   
          <Route path={'binge_bucket'} element={<Protected component = {<Binge_bucket/>}/>}/>    
          <Route path={'recommended'} element={<Protected component = {<Recommended/>}/>}/> 
          <Route path={'addmovies'} element={<Protected component = {<Addmovies/>}/>}/> 
          <Route path={'signup'} element={<Signup/>}/>    
        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
