import React,{useEffect} from "react";
import { useSelector } from "react-redux";
import {selectMoviesList,selectLoginCredential,removeFromBinge,selectLanguageObj,selectBinge_bucket } from "../../redux/movies";
import { useDispatch} from "react-redux";
import i18n from "i18next";
import { useTranslation, initReactI18next } from "react-i18next";
import { enObj ,frObj} from "../../Assets/translation";

const translationsEn = enObj;

const translationsFr = frObj;

i18n
  .use(initReactI18next) // passes i18n down to react-i18next
  .init({
    resources: {
      en: { translation: translationsEn },
      fr: { translation: translationsFr },
    },
    lng: "en",
    fallbackLng: "en",
    interpolation: { escapeValue: false },
  });


const Home = () => {
  const {t} = useTranslation();
  const language = useSelector(selectLanguageObj);
  const loginObj = useSelector(selectLoginCredential)
  const AllMovie = useSelector(selectMoviesList);
  let Bingelist  = [];
  const binge_bucket = useSelector(selectBinge_bucket);
  const dispatch = useDispatch();

   binge_bucket.filter((ele)=>{
      if(ele.user===loginObj.id) Bingelist.push(ele.movie)
  })



  let Movie = AllMovie.filter((ele) => {
    return Bingelist.includes(ele._id);
  });

  useEffect(() => {
    if(language.translate){
     i18n.changeLanguage("fr");
    }
 }, [language.translate])

  const onRemove = (movie_id)=>{
    dispatch(removeFromBinge(movie_id,loginObj.id));
  }

  return (
    <div>
      {Movie.map((ele) => {
        return (
          <div key={ele._id} style={{ margin: "1rem 15rem" }}>
            <div className={"row row-cols-2 g-2 m-2  "}>
              <div className={"col"}>
                <div className={"card"}>
                  <img
                    src={ele.pics}
                    className={"card-img-top"}
                    alt="Palm Springs Road"
                  />
                  <div className={"card-body"}>
                    <h5 className={"card-title"}>{ele.Title}</h5>
                    <p className={"card-text"}>Genre:{ele.Genre}</p>
                    <p className={"card-text"}>
                      <span>Year:</span> {ele.Year}
                    </p>

                    <p className={"card-text"}>Country:{ele.Country}</p>

                    <p className={"card-text"}>Duration:{ele.Runtime}</p>
                  </div>

                  <button
                    className={"btn bg-dark btn-outline-success"}
                    type="submit"
                    onClick={()=>onRemove(ele._id)}
            
                  >
                   {t("removeFromBinge")}
                  </button>
                </div>
              </div>
            </div>
          </div>
        );
      })}
    </div>
  );
};

export default Home;
