import React,{useState} from "react";
import axios from "axios";

const Addmovies = () => {
   const [Title,setTitle] = useState("");
   const [Year,setYear] = useState("");
   const [Runtime,setRuntime] = useState("");
   const [Genre,setGenre] = useState("");
   const [Country,setCountry] = useState("");
   const [pic,setpic] = useState("");

   const onTitleChng = (e)=>{
    setTitle(e.target.value)
   }
   const onYearChng = (e)=>{
    setYear(e.target.value)
   }
   const onRuntimeChng = (e)=>{
    setRuntime(e.target.value)
   }
   const onGenreChng = (e)=>{
    setGenre(e.target.value)
   }
   const onCountryChng = (e)=>{
    setCountry(e.target.value)
   }
   const onPicChng = (e)=>{
    setpic(e.target.value);
   }



   const  onSubmit = async ()=>{
     await axios.post('http://localhost:4000/api/v1/createMovie',{
        Title:Title,
          Year:Year,
          Runtime:Runtime,
          Country:Country,
         Genre:Genre, 
         pics:pic
     }).then(()=>{
        setTitle("") 
        setYear("")
        setRuntime("")
        setGenre("")
        setCountry("")
        setpic("")
        window.alert("Movie Added Successfuly");
     })
     


   } 
  return (
    <div>
      <section className={"vh-10 gradient-custom"}>
        <div className={"container py-1 h-10"}>
          <div
            className={
              "row d-flex justify-content-center align-items-center h-100"
            }
          >
            <div className={"col-12 col-md-8 col-lg-6 col-xl-5"}>
              <div
                className={"card bg-primary text-white"}
                style={{ border_radius: "1rem" }}
              >
                <div className={"card-body p-5 text-center"}>
                  <div className={"mb-md-5 mt-md-4 pb-5"}>
                    <h2 className={"fw-bold mb-2 text-uppercase"}>Add Movie</h2>

                    <div className={"form-outline form-white mb-2"}>
                      <input
                        onChange={onTitleChng}
                        className={"form-control form-control-lg"}
                        type="email"
                        id="typeEmailX"
                        value={Title}
                      />
                      <label className={"form-label"}>Title</label>
                    </div>

                    <div className={"form-outline form-white mb-2"}>
                      <input
                        className={"form-control form-control-lg"}
                        id="typeEmailX"
                        onChange={onYearChng}
                        value={Year}
                      />
                      <label className={"form-label"}>Year</label>
                    </div>

                    <div className={"form-outline form-white mb-2"}>
                      <input
                        className={"form-control form-control-lg"}
                        id="typeEmailX"
                        onChange={onRuntimeChng}
                        value={Runtime}
                      />
                      <label className={"form-label"}>Runtime</label>
                    </div>

                    <div className={"form-outline form-white mb-2"}>
                      <input
                        className={"form-control form-control-lg"}
                        id="typeEmailX"
                        onChange={onGenreChng}
                        value={Genre}
                      />
                      <label className={"form-label"}>Genre</label>
                    </div>

                    <div className={"form-outline form-white mb-2"}>
                      <input
                        className={"form-control form-control-lg"}
                        id="typeEmailX"
                        onChange={onCountryChng}
                        value={Country}
                      />
                      <label className={"form-label"}>Country</label>
                    </div>

                    <div className={"form-outline form-white mb-2"}>
                      <input
                        className={"form-control form-control-lg"}
                        id="typeEmailX"
                        onChange={onPicChng}
                        value={pic}
                      />
                      <label className={"form-label"}>Image Url</label>
                    </div>

                    <button
                      onClick={onSubmit}
                      className={"btn btn-outline-light btn-lg px-5"}
                      type="submit"
                    >
                      Submit
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Addmovies;
