import React from "react";
import { useSelector } from "react-redux";
import { selectMoviesList } from "../../redux/movies";

const Home = () => {
  const AllMovies = useSelector(selectMoviesList);
  const Movie = AllMovies.filter((ele)=>{
    return ele.Year==="2012"
  })
  return (
    <div>
      {Movie.map((ele) => {
        return (
          <div key={ele.id} style={{ margin: "1rem 15rem" }}>
            <div className={"row row-cols-2 g-2 m-2  "}>
              <div className={"col"}>
                <div className={"card"}>
                  <img
                    src={ele.pics}
                    className={"card-img-top"}
                    alt="Palm Springs Road"
                  />
                  <div className={"card-body"}>
                    <h5 className={"card-title"}>{ele.Title}</h5>
                    <p className={"card-text"}>Genre:{ele.Genre}</p>
                    <p className={"card-text"}>
                      <span>Year:</span> {ele.Year}
                    </p>

                    <p className={"card-text"}>Country:{ele.Country}</p>

                    <p className={"card-text"}>Duration:{ele.Runtime}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        );
      })}

  
    </div>
  );
};

export default Home;
