import { createReducer, createAction } from "@reduxjs/toolkit";
import axios from "axios";
import { createAsyncThunk } from "@reduxjs/toolkit";

const initialState = {
  movies: [],
  loginStatus: {
    isLoggedIn: false,
  },
  binge_bucket : [],
  watching_now:[],
  loginObject: {
    id: "",
    pass: "",
  },
  language:{
    translate:false
  }
};

// Create Actions

// Action for login and logout

export const handleLoginLogout = createAction("loginOrLogout", (authResult) => {
  return { payload: { useValid: authResult } };
});

// Action for setting loginObj

export const setLoginObj = createAction("setLoginObj", (id, pass) => {
  return {
    payload: {
      user_id: id,
      user_pass: pass,
    },
  };
});

//Action adding to watch_now

export const addToWatch = createAction('add/wacth_now',(user_id,movie_id)=>{
  return {
    payload :{
      movie_id :movie_id,
      user_id:user_id,
    }
  }
})

//Action removing from watch_now 

export const removeFromWatch = createAction('remove/watch',(user_id,movie_id)=>{
  return {
    payload:{
      movie_id:movie_id,
      user_id:user_id
    }
  }
})

// Action for adding to binge_bucket

export const updateBinge = createAction("addToBinge", (movie_id, user_name) => {
  return {
    payload: {
      movie_id: movie_id,
      user_name: user_name,
    },
  };
});

// action for removing from binge_bucket 

export const removeFromBinge = createAction("removeFromBinge",(movie_id,user_name)=>{
     return {
      payload:{
        movie_id:movie_id,
        user_name:user_name
      }
     }
})

// action for tranlation
export const  translateContent= createAction("translate/content",(doTranslate)=>{
 
  return {
    payload:{
      trans:doTranslate
    }
  }
})





//  Movies-Selector
const selectMoviesState = (rootState) => rootState.movies;
export const selectMoviesList = (rootState) =>
  selectMoviesState(rootState).movies;

// loginSate selector
const selectLoginReport = (rootState) => rootState.loginStatus;
export const loginStatus = (rootState) =>
  selectLoginReport(rootState).loginStatus;

// loginObj selector
const selectLoginObj = (rootState) => rootState.loginObject;
export const selectLoginCredential = (rootState) =>
  selectLoginObj(rootState).loginObject;

// wathcing_now selector 
const selectWathcing_now = (rootState)=> rootState.watching_now;
export const slelectWatching = (rootState)=>selectWathcing_now(rootState).watching_now;

// binge_bucket selector
const selectBinge = (rootState)=>rootState.binge_bucket;
export const selectBinge_bucket = (rootState)=>selectBinge(rootState).binge_bucket;


// fetching data using asyncThunk
 export const getAllMovies = createAsyncThunk('movies/getAllMovies',async(rejectWithValue)=>{
  try {
    const { data } = await axios.get(`http://3.109.143.147:4000/api/v1/`);
    return data;
  } catch (error) {
    return rejectWithValue(error.message);
  }
})

// selector for translate;
const selectLanguage = (rootState)=>rootState.language;
export const selectLanguageObj = (rootState)=>selectLanguage(rootState).language 

const reducer = createReducer(initialState, {
  [handleLoginLogout]: (state, action) => {
    state.loginStatus.isLoggedIn = action.payload.useValid;
  },
  [setLoginObj]: (state, action) => {
    state.loginObject.id = action.payload.user_id;
    state.loginObject.pass = action.payload.user_pass;
  },
  [updateBinge]: (state, action) => {
    for (let i = 0; i < state.movies.length; i++) {
      if (state.movies[i]._id === action.payload.movie_id) {
          state.binge_bucket.push({user:action.payload.user_name,movie:action.payload.movie_id});
      }
    }

     
  },
  [removeFromBinge]:(state,action)=>{
    for (let i = 0; i < state.binge_bucket.length; i++) {
      if (state.binge_bucket[i].movie === action.payload.movie_id) {
        state.binge_bucket.splice(i,1);
      }
    }
  },
  [translateContent]:(state,action)=>{
    state.language.translate = action.payload.trans;
  },
  [getAllMovies.fulfilled]:(state,{payload})=>{
    state.movies = payload
  },
  [addToWatch]:(state,action)=>{
  
    for (let i = 0; i < state.movies.length; i++) {
      if (state.movies[i]._id === action.payload.movie_id) {
          state.watching_now.push({user:action.payload.user_id,movie:action.payload.movie_id});
      }
    }

    
  },
  [removeFromWatch]:(state,action)=>{
    for (let i = 0; i < state.watching_now.length; i++) {
      if (state.watching_now[i].movie === action.payload.movie_id) {
        state.watching_now.splice(i,1);
      }
    }
  }
  
});
export default reducer;
